<template>
  <div>
    <van-sticky>
      <van-nav-bar title="物流信息" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <div v-if="logisticsInfo.length == 0" class="noData">
      暂无物流信息!
    </div>
    <!-- 信息 -->
    <van-steps direction="vertical" :active="0" active-color="#333333" v-else>
      <van-step v-for="item in logisticsInfo">
        <div>{{ item.context }}</div>
        <p>{{ item.ftime }}</p>
      </van-step>
    </van-steps>
  </div>
</template>

<script>
import { logisticsInfo } from "@/api/order";
export default {
  data() {
    return {
      id: "",
      logisticsInfo: []
    };
  },
  created() {
    this.id = this.$route.query.id;
    logisticsInfo(this.id).then(res => {
      this.logisticsInfo = res;
    });
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less" scoped>
.van-steps--vertical {
  width: 345px;
  box-shadow: 0 0 10px 2px #f5f5f5;
  margin:15px auto;
  box-sizing: border-box;
}
/deep/ .van-icon {
  font-size: 20px;
}
/deep/ .van-step__circle {
  width: 20px;
  background: #dcdcdc;
  height: 20px;
}
/deep/ .van-icon-checked {
  color: #de4f53 !important;
}
</style>
